import React, { Component } from "react";
import { motion } from "framer-motion";
import { AiOutlineLeftCircle } from "react-icons/ai";
import { AiOutlineRightCircle } from "react-icons/ai";

class CardsContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      position: 0,
    };
    this.images = [
      {
        id: 1,
        src: "/images/project1.jpg",
        link: "https://zarghamkhandev.github.io/Werkcation-Real-Estate/",
      },
      {
        id: 2,
        src: "/images/project1.jpg",
        link: "https://zarghamkhandev.github.io/Werkcation-Real-Estate/",
      },
      {
        id: 3,
        src: "/images/project1.jpg",
        link: "https://zarghamkhandev.github.io/Werkcation-Real-Estate/",
      },
      {
        id: 4,
        src: "/images/project1.jpg",
        link: "https://zarghamkhandev.github.io/Werkcation-Real-Estate/",
      },
    ];
  }

  rightScrollHandler = () => {
    console.log(this.state.position);
    if (this.state.position < this.images.length - 1) {
      this.setState({ position: this.state.position + 1 });
    }
  };
  leftScrollHandler = () => {
    if (this.state.position > 0) {
      this.setState({ position: this.state.position - 1 });
    }
  };

  render() {
    return (
      <div>
        <motion.div
          className=" flex flex-row overflow-x-auto disable-scrollbars "
          initial={{ scrollLeft: 0 }}
          animate={{ scrollLeft: this.state.position * 272 }}
          transition={{ type: "spring", stiffness: 260, damping: 20 }}
        >
          {this.images.map((image) => {
            return (
              <motion.div
                className="p-1 bg-white rounded-lg shadow-md flex-shrink-0 w-64 m-2"
                key={image.id}
              >
                <a href={image.link} target="_blank">
                  <img
                    src={process.env.PUBLIC_URL + image.src}
                    alt="project1"
                    className="rounded w-full object-cover"
                  ></img>
                </a>
              </motion.div>
            );
          })}
        </motion.div>
        <div className="flex justify-center py-2 text-mygray">
          <span className="px-1" onClick={this.leftScrollHandler}>
            <AiOutlineLeftCircle
              size={24}
              className="cursor-pointer hover:scale-125 transform duration-300"
            />
          </span>
          <span
            className="px-1 cursor-pointer"
            onClick={this.rightScrollHandler}
          >
            <AiOutlineRightCircle
              size={24}
              className="cursor-pointer hover:scale-125 transform duration-300"
            />
          </span>
        </div>
      </div>
    );
  }
}

export default CardsContainer;
